export default {
	data() {
		return {
			src: '../../static/img/author.jpg',
			sex: ['男', '女'],
			user: {
				userName: '哈哈哈',
				intrduction: '',
				sex: '',
				birthday: '',
				city: ''
			}
		}
	},
	methods: {
		chooseImg() {
			uni.chooseImage({
				count: 1,
				sourceType: ['album'],
				sizeType: ['original', 'compressed'],
				success: (res) => {
					this.src = res.tempFilePaths
				}
			})
		},
		bindSexChange(e) {
			this.user.sex = this.sex[e.target.value]
			uni.setStorage({
				key: 'sex',
				data: this.sex[e.target.value],

			})
		},
		bindDateChange(e) {
			this.user.birthday = e.target.value
			uni.setStorage({
				key: 'birthday',
				data: e.target.value,

			})
		},
		bindCityChange(e) {
			this.user.city = e.target.value[0]
			uni.setStorage({
				key: 'city',
				data: e.target.value[0]
			})
		}
	},
	onShow() {
		uni.getStorage({
			key: 'userName',
			success: (res) => {
				this.user.userName = res.data
			}
		})
		uni.getStorage({
			key: 'intrduction',
			success: (res) => {
				this.user.intrduction = res.data
			}
		})
		uni.getStorage({
				key: 'sex',
				success: (res) => {
					this.user.sex = res.data
				}
			}),
			uni.getStorage({
				key: 'birthday',
				success: (res) => {
					this.user.birthday = res.data
				}
			}),
			uni.getStorage({
				key: 'city',
				success: (res) => {
					this.user.city = res.data
				}
			})
	},
}
